'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { supabase } from '@/lib/supabase';

type Store = {
  id: string;
  name: string;
  city: string;
  type: string;
  alias: string;
};

export default function ManageStorePage() {
  const [stores, setStores] = useState<Store[]>([]);
  const router = useRouter();

  useEffect(() => {
    const fetchStores = async () => {
      const { data, error } = await supabase
        .from('store_admin')
        .select('id, name, city, type, alias');

      if (!error && data) setStores(data);
    };

    fetchStores();
  }, []);

  return (
    <main className="flex-1 p-4 md:p-6 bg-gray-50">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-2 mb-4">
        <h2 className="text-xl font-semibold text-gray-800">All Stores</h2>
        <Button onClick={() => router.push('/admin/dashboard/manage-store/create')}>
          Create Store
        </Button>
      </div>

      {/* Mobile Card View */}
      <section className="grid gap-4 md:hidden">
        {stores.map((store) => (
          <div
            key={store.id}
            onClick={() => router.push(`/admin/dashboard/manage-store/${store.id}`)}
            className="bg-white rounded-md shadow p-4 space-y-1 cursor-pointer hover:bg-gray-50"
          >
            <h3 className="font-semibold text-gray-900">{store.name}</h3>
            <p className="text-sm text-gray-600">{store.city}</p>
            <p className="text-xs text-gray-500 capitalize">
              {store.type} Â· {store.alias}
            </p>
            <Button size="sm" className="mt-2 w-fit">
              View / Edit
            </Button>
          </div>
        ))}
      </section>

      {/* Desktop Table View */}
      <section className="hidden md:block bg-white border rounded-md shadow overflow-x-auto">
        <table className="min-w-full text-sm text-gray-700">
          <thead className="bg-gray-100">
            <tr>
              <th className="px-4 py-2 text-left">Name</th>
              <th className="px-4 py-2 text-left">City</th>
              <th className="px-4 py-2 text-left">Type</th>
              <th className="px-4 py-2 text-left">Alias</th>
              <th className="px-4 py-2 text-left">Actions</th>
            </tr>
          </thead>
          <tbody>
            {stores.map((store) => (
              <tr key={store.id} className="border-t hover:bg-gray-50">
                <td className="px-4 py-2">{store.name}</td>
                <td className="px-4 py-2">{store.city}</td>
                <td className="px-4 py-2 capitalize">{store.type}</td>
                <td className="px-4 py-2">{store.alias}</td>
                <td className="px-4 py-2">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => router.push(`/admin/dashboard/manage-store/${store.id}`)}
                  >
                    View / Edit
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </section>
    </main>
  );
}
